import { Injectable } from '@angular/core';
import { Resolve, Router, ActivatedRouteSnapshot } from '@angular/router';
import { IDesk } from '../models/IDesk';
import { PlApprovalService } from '../services/plApproval.service';
import { Observable } from 'rxjs';

@Injectable()
export class ConfigResolver implements Resolve<IDesk[]> {
    constructor(
        private plApprovalService: PlApprovalService, 
        private router: Router,
        ) {}

    resolve(route: ActivatedRouteSnapshot): Observable<IDesk[]> {
        return this.plApprovalService.getDesks();
    }
}
