import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { PLApprovalComponent } from './PLApproval/PLApproval.component';
import { ConfigResolver } from './resolver/config.resolver';


const routes: Routes = [
  {path: 'login', component: LoginComponent, resolve: {desks: ConfigResolver}},
  {path: 'plapproval', component: PLApprovalComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {useHash: true})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
